<template>
  <div>
    <div v-if="shown">
      Add app to home screen?
      <div class="button-group">
        <button type="button" class="button" @click="installPWA">
          Install!
        </button>

        <button type="button" class="button" @click="dismissPrompt">
          No, thanks
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    shown: false,
    installEvent: null
  }),

  beforeMount() {
    window.addEventListener('beforeinstallprompt', (e) => {
      this.message = e
      e.preventDefault()
      this.installEvent = e
      this.shown = true
    })
  },

  methods: {
    dismissPrompt() {
      this.shown = false
    },

    installPWA() {
      this.installEvent.prompt()
      this.installEvent.userChoice.then((choice) => {
        this.dismissPrompt() // Hide the prompt once the user's clicked
        if (choice.outcome === 'accepted') {
          // Do something additional if the user chose to install
        } else {
          // Do something additional if the user declined
        }
      })
    },
  }
}
</script>
